.access {
  position: relative;
  float: left;
  width: 100%;

  &__title {
    position: relative;
    width: 100%;
    color: #315479;
    font-family: $OpenSans;
    font-size: 32px;
    font-weight: 400;
    line-height: 43px;
    text-align: center;
  }

  &__box {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 50px;
  }

  &__descr-wrap {
    position: relative;
    width: 50%;

    @include breakpoint(max-width 1024px) {
      width: 100%;
    }
  }

  &__sub-title {
    position: relative;
    width: 100%;
    font-family: $OpenSans;
    font-weight: 400;
    color: #315479;
    font-size: 25px;
    line-height: 31px;
    margin-top: 85px;
  }

  &__descr {
    position: relative;
    width: 100%;
    font-family: $OpenSans;
    font-weight: 400;
    color: #677482;
    font-size: 23px;
    line-height: 33px;
    margin-top: 45px;
  }

  &__sub-descr {
    position: relative;
    width: 98%;
    font-family: $OpenSans;
    font-weight: 400;
    color: #677482;
    font-size: 23px;
    line-height: 33px;
    margin-top: 45px;
  }

  &__img-shell {
    position: relative;
    width: 50%;
    margin-top: 60px;

    @include breakpoint(max-width 1024px) {
      width: 100%;
      order: 1;
    }
  }

  &__img-wrap {
    position: relative;
    width: 550px;
    height: 400px;
    overflow: hidden;

    @include breakpoint(max-width 1024px) {
      margin: 0 auto;
    }

    @include breakpoint(max-width 480px) {
      width: 300px;
      height: 218px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
}