.faq {
  position: relative;
  float: left;
  width: 100%;

  &__title {
    position: relative;
    width: 100%;
    text-align: center;
    color: #315479;
    font-family: $OpenSans;
    font-size: 36px;
    font-weight: 400;
    line-height: 43px;
    margin-top: 75px;
  }

  &__box {
    position: relative;
    width: 100%;
    margin-top: 75px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__element {
    position: relative;
    width: 32%;

    @include breakpoint(max-width 1024px) {
      width: 45%;
    }

    @include breakpoint(max-width 768px) {
      width: 100%;
    }
  }

  &__comment {
    position: relative;
    width: 100%;
    height: 225px;
    background-image: url("../img/comment-1.png");
    background-repeat: no-repeat;
    background-size: cover;
    padding: 40px 45px;
    box-sizing: border-box;

    @include breakpoint(max-width 480px) {
      height: 250px;
    }

    &--first {
      background-image: url("../img/comment-2.png");
      height: 175px;
    }

    &--second {
      background-image: url("../img/comment-3.png");
      height: 160px;
    }

    &:after {
      position: absolute;
      content: "";
      display: block;
      width: 40px;
      height: 40px;
      background-color: #158af4;
      bottom: -9px;
      left: 40px;
      transform: rotate(-30deg);
    }
  }

  &__descr {
    position: relative;
    color: white;
    font-family: $OpenSans;
    font-size: 17px;
    font-weight: 400;
    line-height: 25px;

    &:before {
      position: absolute;
      content: "";
      display: block;
      width: 25px;
      height: 20px;
      background-image: url("../img/dots.png");
      background-repeat: no-repeat;
      background-size: contain;
      left: -33px;
      top: -6px;
    }
  }

  &__author {
    position: relative;
    width: 90%;
    margin-top: 25px;
    margin-left: 20px;

    &-title {
      position: relative;
      float: left;
      width: 90%;
      font-family: $OpenSans;
      font-weight: 400;
      color: #315479;
      font-size: 16px;
      line-height: 21px;
      margin-bottom: 0;
    }

    &-descr {
      position: relative;
      float: left;
      width: 90%;
      font-family: $OpenSans;
      font-weight: 400;
      color: #677482;
      font-size: 14px;
      line-height: 23px;
    }
  }
}